@font-face{
    font-family: Roboto;
    src: url(${___CSS_LOADER_URL_REPLACEMENT_0___});
}

*{
    box-sizing: border-box;
}

:root {
    line-height: 1.5;
    --background-color: #121212;
    --text-primary-color: #ffffff;
    --text-secondary-color: #b3b3b3;
    --selected-color: #5851db;
    --hover-color: #7270f5;
    --accent-color: #A2EDFC;
    --card-background: #1e1e1e;
    --border-color: #333333;
    
    --fw-thin: 100;
    --fw-regular: 500;
    --fw-bold: 600;
    --fw-black: 700;

    --font-size-small: 1.2rem;
    --font-size-main: 1.5rem;
    --font-size-large: 2.2rem;
    --font-size-header: 2.6rem;
    
    --tiny-padding: 0.7rem;
    --small-padding: 1rem;  
    --medium-padding: 1.5rem;  
    --large-padding: 2.25rem;   
    --Xl-padding: 5rem;
}


h1, h2, h3, h4, h5, figure, p, ol, ul {
    margin: 0;
    padding: 0;
}

p{
    font-size: var(--font-size-small);
    font-weight: var(--fw-thin);
    opacity: 0.6;
}


ol[role="list"], ul[role="list"] {
    list-style: none;
    padding-inline: 0;
}

li{
    list-style: none;
}

h1, h2, h3, h4, h5 {
    font-size: inherit;
    font-weight: inherit;
}

img {
    display: block;
    max-inline-size: 100%;
}

button,
input,
select,
textarea {
    font-family: inherit;
    font-size: 100%;
}

a:hover, button:hover, input[type="checkbox"]{
    cursor: pointer; 
}

/* CSS reset - end */
/*------------------------------------------------------------*/
/* Global - start */
body{
    margin: 0;
    padding: 0;
    font-family: Roboto, sans-serif;
    font-size: var(--font-size-main);
    font-weight: var(--fw-regular);
    height: 100vh;
    width: 100vw;
    background-color: var(--background-color); 
    color: var(--color-text-main);
    display: grid;
    grid-template-columns: 18.75rem 1fr;
}

h1{
    font-size: var(--font-size-header);
    font-weight: var(--fw-black);
    color: var(--text-primary-color);
}

h2{
    font-size: var(--font-size-large);
    font-weight: var(--fw-bold);
    color: var(--text-primary-color);
}

h3{
    font-size: var(--font-size-small);
    font-weight: var(--fw-regular);
    color: var(--text-primary-color);
}

p{
    font-size: var(--font-size-small);
    color: var(--text-secondary-color);
}

a {
    color: var(--accent-color);
  }
/* Modal styling */

.modal-container{
    visibility: hidden;
    position: absolute;
}

.modal {
    border: none;
    border-radius: 8px;
    box-shadow: 0 4px 10px rgba(0, 0, 0, 0.2);
    padding: 20px;
    width: 400px;
    max-width: 90%;
    background: #fff;
    font-size: 16px;
   
}

/* Modal heading */
.modal h2 {
    margin-top: 0;
    font-size: 1.5rem;
    color: #333;
    text-align: center;
    border-bottom: 1px solid #ddd;
    padding-bottom: 10px;
    margin-bottom: 20px;
}

/* Form row styles */
.form-row {
    margin-bottom: 15px;
}

.form-row label {
    display: block;
    font-weight: bold;
    margin-bottom: 5px;
    color: #555;
}

.form-row input,
.form-row select {
    width: 100%;
    padding: 8px;
    font-size: 14px;
    border: 1px solid #ccc;
    border-radius: 4px;
    transition: border-color 0.2s;
}

.form-row input:focus,
.form-row select:focus {
    outline: none;
    border-color: #007bff;
}

/* Modal buttons */
.modal-btns {
    display: flex;
    justify-content: space-between;
    margin-top: 20px;
}

.modal-btns .add-btn,
.modal-btns .close-btn {
    padding: 10px 20px;
    border: none;
    border-radius: 4px;
    cursor: pointer;
    font-size: 14px;
    font-weight: bold;
    transition: background-color 0.3s;
}

.modal-btns .add-btn {
    background-color: #007bff;
    color: #fff;
}

.modal-btns .add-btn:hover {
    background-color: #0056b3;
}

.modal-btns .close-btn {
    background-color: #f44336;
    color: #fff;
}

.modal-btns .close-btn:hover {
    background-color: #c72c2c;
}

/* Modal - End*/
/*------------------------------------------------------------*/
/*------------------------------------------------------------*/
/* SideBar - Start*/
.sidebar{
    grid-column: 1/2;
    grid-row:1/3; 
    background-color: var(--background-color);
    padding: 0px var(--large-padding);
    gap: var(--large-padding);
    display: grid;
    grid-template-rows: 4.25rem 1fr;
    font-size: var(--font-size-small);
    /*border-right: 1px solid var(--text-primary-color);*/
}

.sidebar-header{
   grid-row: 1/2;
    
}

.nav{
    grid-row: 2/3;
    display: flex;
    flex-direction: column;
    gap: var(--large-padding);
}

.sidebar button, .task button{
    padding:0;
    border: none;
    background: none;
    width:100%;
    text-align: left;
    color: var(--text-primary-color);
    border-radius: 0.5em;
    padding: 0.2em;
    transition: background-color 0.3s;
}

.highlight * {
    fill:var(--accent-color);
    color:var(--accent-color);
}
.sidebar button:hover {
    background:#90bacf18;
}

.sidebar button:focus * {
    fill:var(--accent-color);
    color:var(--accent-color);
}


.sidebar svg{
    fill: white;
    width: 1.75em;
    height:auto;
    
}
.sidebar svg path{
    stroke-width: 0px;
}


/* SideBar - End*/
/*------------------------------------------------------------*/
/* Content- Start*/

.page{
    grid-row:1/3;
    grid-column:2/3;
    display: grid;
    grid-template-rows: 4.25rem 1fr;
    grid-template-columns: 1fr clamp( 8rem, 30%, 20rem);
    padding-left: 5rem;
    row-gap: var(--large-padding);
    background-color: var(--card-background);
}

.page-nav{
    grid-column:1/2;
    grid-row:1/2;
}

.page-body{
    grid-column:1/2;
    grid-row:2/3;
    display:flex;
    flex-direction: column;
    row-gap: var(--medium-padding);
    
}


.page-header{
    /*grid-column:1/2;
    grid-row:2/3;
    display:flex;
    align-items: flex-start;*/
    
}

.content{
    grid-row:2/3;
    grid-column:1/2;
    display:flex;
    flex-direction: column;
    color: var(--text-primary-color);
}

.task{
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding-bottom: var(--small-padding);
    color: var(--text-primary-color);
}

.task img{
    filter:invert(1);
    width: 1em;
    height:auto;
}


/*------------------------------------------------------------*/
/* Utility- Start*/
.column-flex{
    display: flex;
    flex-direction: column;
    gap: 1.2rem;
}

.row-flex{
    display:flex;
    align-items: center;
    gap: var(--small-padding);
}

.row-flex.start-end{
    justify-content: space-between;
}

.row-flex.wide, .column-flex.wide {
    gap: var(--large-padding);
}

.row-flex.small, .column-flex.small{
    gap: var(--tiny-padding);
}

.underline {
    position: relative;
}

.underline::after {
    content: '';
    position: absolute;
    left: 0;
    bottom: 0; /* Position it at the bottom */
    width: 100%;
    height: 1px; /* Thickness of the underline */
    background-color: var(--text-secondary-color); /* Color of the underline */
    opacity: 0.5;
}


input[type="checkbox"] {
    appearance: none;
    -webkit-appearance: none;
    display: flex;
    align-content: center;
    justify-content: center;
    font-size: 2rem;
    border: 0.15rem solid var(--text-secondary-color);
    border-radius: var(--font-size-main);;
}
  
input[type="checkbox"]::before {
    content: "";
    width: 1.5rem;
    height: 1.5rem;
    clip-path: circle(50% at 50% 50%);
    transform: scale(0);
    background-color: var(--accent-color);
    opacity: 20%;
}

input[type="checkbox"]:checked{
    border-color: var(--accent-color);
}

input[type="checkbox"]:checked::before {
    transform: scale(1);
}




  
  



